import { Component, Input, OnInit } from '@angular/core';
import { OmniPullService } from 'src/app/commonBussinessSRC/psServices/omni-common/omni-pull.service';
import { PsSelectDropdownComponent } from 'src/app/commonSRC/psComponents/ps-base/ps-base-field/ps-field-select/ps-select-dropdown/ps-select-dropdown.component';
import { LoggerService } from 'src/app/commonSRC/psServices/logger/logger.service';
import { IchangeValues, IOptionsPsSelectDropdown } from 'src/app/commonSRC/psServices/models/ps-common-interface';
import { PsCommonService } from 'src/app/commonSRC/psServices/ps-common/ps-common.service';
import { IOptionsPsDropdownNationalitiesExposed } from '../ps-dropdown-nationalities/ps-dropdown-nationalities.component.interfaces';

@Component({
  selector: 'ps-dropdown-designations',
  templateUrl: './ps-dropdown-designations.component.html',
  styleUrls: ['./ps-dropdown-designations.component.scss'],
})
export class PsDropdownDesignationsComponent extends PsSelectDropdownComponent implements OnInit {
  @Input() options: IOptionsPsDropdownNationalitiesExposed;
  public defaultSelectOptions: IOptionsPsSelectDropdown = {};

  constructor(public commonProv: PsCommonService, public loggerP: LoggerService, private omniPull: OmniPullService) {
    super(commonProv, loggerP);
  }

  ngOnInit() {
    this.commonProv.copyObject(this.defaultSelectOptions, this.options, false);
    this.returnDesignations();
  }

  private async returnDesignations() {
    const designationsResponse = await this.omniPull.returnDesignations().catch(error => {
      this.logger.error('Error! while fetching designations in PsDropdownDesignationsComponent', error);
    });

    this.defaultSelectOptions.listOfOptions = [];
    if (designationsResponse && designationsResponse.gridModel && designationsResponse.gridModel.length > 0) {
      for (const iterator of designationsResponse.gridModel) {
        const position: any = {
          itemValue: iterator.positionCode,
          description: iterator.longDescription,
          selectedObj: iterator
        };
        this.defaultSelectOptions.listOfOptions.push(position);
      }
      this.options.listOfOptions = [];
      this.commonProv.copyObject(this.options.listOfOptions, this.defaultSelectOptions.listOfOptions, false);
    }
  }

  public onChange(value: IchangeValues) {
    this.onPsChange.emit(value);
  }

}
